import React from "react";
import { Route, Switch, useLocation } from "react-router";
import { BrowserRouter } from "react-router-dom";
import "./App.css";
import Navbar from "./components/Navbar";
import Home from "./pages/Home";
import Committee from "./pages/Committee";
import Mudae from "./pages/Mudae";
import Events from "./pages/Events";
import { AnimatePresence } from "framer-motion";

function App() {
  const location = useLocation();
  return (
    <>
      <Navbar />
      <AnimatePresence>
        <Switch location={location} key={location.key}>
          <Route path="/MUAS/" exact component={Home} />
          <Route path="/MUAS/events" exact component={Events} />
          <Route path="/MUAS/committee" exact component={Committee} />
          <Route path="/MUAS/mudae" exact component={Mudae} />
        </Switch>
      </AnimatePresence>
    </>
  );
}

export default App;
