import { AnimatePresence, motion } from "framer-motion";
import React from "react";
import "./LeaderboardListItem.css";

interface listProps {
  name: string;
  messages: number;
  xp: number;
  image: string;
  level: number;
  counter: number;
}

function LeaderboardListItem(props: listProps) {
  return (
    <motion.div
      className="leaderboard-card "
      initial={{ opacity: 0, y: 20 }}
      animate={{ opacity: 1, y: 0 }}
      transition={{ duration: 0.6, delay: 0.1 }}
      exit={{ y: 20, opacity: 0 }}
    >
      <div className="lb-container">
        <div className="bg-num">{props.counter}</div>
        <div className="stats">
          <div className="card-name-lb">{props.name}</div>
          <div className="three-stats">
            <div className="lb-one-stat">
              <h2>Level</h2> {props.level}
            </div>
            <div className="lb-one-stat">
              <h2>Xp</h2> {props.xp}
            </div>
            <div className="lb-one-stat">
              <h2>Messages</h2> {props.messages}
            </div>
          </div>
        </div>
        <div className="playerImg">
          <img className="player-img" src={props.image} alt={props.name} />
        </div>
      </div>
    </motion.div>
  );
}

export default LeaderboardListItem;
